import { Injectable } from '@angular/core';
import { ContentLink } from './content-link';
import { ListItemsService } from 'src/app/services/list-items-service';

@Injectable({
  providedIn: 'root'
})
export class ContentLinksService extends ListItemsService<ContentLink> {

  getListTitle(): string {
    return 'Контент личной страницы сотрудника';
  }

  getListRelativePath(): string {
    return 'Lists/slContentLinks';
  }

  getSelect(): string {
    return 'Id,Title,slLink,slIsText,slOrder,'
    + 'slDirectionLookup/Id,slDirectionLookup/Title,slDirectionLookup/slCode,'
    + 'AttachmentFiles/ServerRelativeUrl';
  }

  getExpand(): string {
    return 'slDirectionLookup,AttachmentFiles';
  }

  getOrderBy(): [string, boolean][] {
    return [['slOrder', true]];
  }

  getTop(): number {
    return 5000;
  }

  getCachingOptions(): import('@pnp/odata').ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): ContentLink {
    const entity: ContentLink = {
      id: item.Id,
      title: item.Title,
      link: item.slLink,
      isText: item.slIsText,
      order: item.slOrder
    };
    if (item.slDirectionLookup) {
      entity.directionIds = item.slDirectionLookup.map(i => i.Id);
      entity.directionTitles = item.slDirectionLookup.map(i => i.Title);
    }
    if (item.AttachmentFiles) {
      entity.imageUrl = item.AttachmentFiles.map(a => a.ServerRelativeUrl)[0];
    }
    return entity;
  }

  convertEntityToListItem(entity: ContentLink) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): ContentLink[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
