import { Component, OnInit, NgZone } from '@angular/core';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { ActivatedRoute } from '@angular/router';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { RequestsModalService } from '../../../requests/services/requests-modal/requests-modal.service';
import { RequestsService } from '../../../requests/services/requests/requests.service';
import { Request } from '../../../requests/services/requests/request';
import { RequestsTemplate } from '../../../requests/services/requests-templates/requests-template';
import { RequestsStatus } from 'src/app/requests/services/requests-statuses/requests-status';
import { RequestsWorkflowService } from 'src/app/requests/services/requests-workflow/requests-workflow.service';

@Component({
  selector: 'app-requests',
  templateUrl: './requests.component.html',
  styleUrls: ['./requests.component.scss']
})
export class RequestsComponent implements OnInit {

  private url = '';
  private webId: string = undefined;
  showBids = false;
  showHistory = false;
  private pages: IListItemsPage<Request>[] = [];
  private index = 0;
  private userId: number = null;
  private sortField = 'Created';
  private sortOrder = false;
  public hasPrev = false;
  public hasNext = false;
  private statuses: RequestsStatus[] = null;
  public templates: RequestsTemplate[] = [];

  public requests: Request[] = [];

  public requestTemplates: RequestsTemplate[] = null;

  constructor(
    private requestsModalService: RequestsModalService,
    private sharepointService: SharepointService,
    private zone: NgZone,
    private requestWorkflowService: RequestsWorkflowService,
    private route: ActivatedRoute
  ) {}

  public openModal() {
    this.requestsModalService.openModal();
  }

  public next(): void {
    if (this.hasNext) {
      if (this.index === this.pages.length - 1) {
        this.pages[this.index]
          .getNext()
          .subscribe((page: IListItemsPage<Request>) => {
            this.index++;
            this.zone.run(() => {
              this.requests = page.items;
              this.hasNext = page.hasNext;
              this.hasPrev = true;
            });
            this.pages.push(page);
            this.scrollUp();
          });
      } else {
        this.index++;
        this.requests = this.pages[this.index].items;
        this.hasNext = this.pages[this.index].hasNext;
        this.hasPrev = true;
        this.scrollUp();
      }
    }
  }

  public prev(): void {
    if (this.hasPrev) {
      this.index--;
      this.zone.run(() => {
        this.requests = this.pages[this.index].items;
        this.hasNext = true;
        this.hasPrev = this.index > 0;
        this.scrollUp();
      });
    }
  }

  public sortByTheme() {
    if (this.sortField === 'Title') {
      this.sortOrder = !this.sortOrder;
    } else {
      this.sortField = 'Title';
      this.sortOrder = true;
    }
    this.load();
  }

  public sortByDate() {
    if (this.sortField === 'Created') {
      this.sortOrder = !this.sortOrder;
    } else {
      this.sortField = 'Created';
      this.sortOrder = true;
    }
    this.load();
  }

  public sortByStatus() {
    if (this.sortField === 'slRequestStatusLookup/Title') {
      this.sortOrder = !this.sortOrder;
    } else {
      this.sortField = 'slRequestStatusLookup/Title';
      this.sortOrder = true;
    }
    this.load();
  }

  public filterRequestsByType(type: RequestsTemplate, event: any) {
    if (event.target.checked) {
      this.templates.push(type);
    } else {
      this.templates.splice(this.templates.indexOf(type), 1);
    }
    this.load();
  }

  public toBegin() {
    this.load();
  }

  private resetPage() {
    this.zone.run(() => {
      this.pages = [];
      this.index = 0;
      this.hasNext = false;
      this.hasPrev = false;
      // this.scrollUp();
    });
  }

  public load() {
    this.requestWorkflowService.getRequestsByUserAndTemplatePaged(
      this.userId,
      [[this.sortField, this.sortOrder]],
      this.webId,
      this.templates
    ).subscribe((p: IListItemsPage<Request>) => {
        this.zone.run(() => {
          this.resetPage();
          this.requests = p.items;
          this.hasNext = p.hasNext;
        });
        this.pages.push(p);
      });
  }

  private scrollUp() {
    this.zone.run(() => {
      document.getElementById('s4-workspace').scrollTop = 0;
    });
  }

  ngOnInit() {
    this.url = this.route.snapshot.data.company && this.route.snapshot.data.company.url;
    this.webId =
      this.route.snapshot.data.webId !== 'root'
        ? this.route.snapshot.data.webId
        : null;
    this.userId = this.sharepointService.getCurrentUserContext().userId;
    this.requestTemplates = this.route.snapshot.data.templates;
    this.statuses = this.route.snapshot.data.statuses;
    this.load();
  }
}
