import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PersonalPageComponent } from './components/personal-page/personal-page.component';
import { RequestsGroupsResolver } from '../requests/services/requests-groups/requests-groups.resolver';
import { RequestsTemplatesResolver } from '../requests/services/requests-templates/requests-templates.resolver';
import { CurrentUserWebResolver } from '../services/companies/current-user-web.resolver';
import { CompanyResolver } from '../services/companies/company.resolver';
import { CurrentUserCompanyResolver } from '../services/companies/current-user-company.resolver';
import { RequestsStatusesResolver } from '../requests/services/requests-statuses/requests-statuses.resolver';
// import { MsalGuard } from '@azure/msal-angular';


const routes: Routes = [
  {
    path: '',
    component: PersonalPageComponent,
    // canActivate : [MsalGuard],
    resolve: {
      groups: RequestsGroupsResolver,
      templates: RequestsTemplatesResolver,
      webId: CurrentUserWebResolver,
      company: CurrentUserCompanyResolver,
      statuses: RequestsStatusesResolver
    },
    data: { animation: 'fadeAnimation', title: 'Страница сотрудника' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [RequestsGroupsResolver, RequestsTemplatesResolver, CurrentUserWebResolver, CurrentUserCompanyResolver],
    exports: [RouterModule]
})
export class PersonalRoutingModule { }
