import { Component, OnInit } from '@angular/core';
import { ContentLinksService } from '../../services/content-links/content-links.service';
import { ContentLink } from '../../services/content-links/content-link';
import { ImagesService } from 'src/app/services/images/images.service';
import { CompaniesService } from 'src/app/services/companies/companies.service';
import { Company } from 'src/app/services/companies/company';

export interface ItemsInLine {
  items: ContentLink[];
}

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit {
  public itemsText: ContentLink[] = [];
  public itemsInLine: ItemsInLine[] = [];
  private company: Company = null;

  constructor(
    private contentLinksService: ContentLinksService,
    private companiesService: CompaniesService,
    private imagesService: ImagesService
  ) {}

  public prepareLink(link: string): string {
    return '"' + this.imagesService.prepareBackgroundUrl(link) + '"';
  }

  public getItemsImg(list: ContentLink[]) {
    const items = list.filter(item => !item.isText);
    const length = items.length;
    const mod = Math.floor(length / 2);
    const counLines = mod + (length - mod * 2);
    let start = 0;
    for (let i = 0; i < counLines; i++) {
      const lineItems = items.slice(start, start + 2);
      this.itemsInLine.push({
        items: lineItems
      });
      start = start + 2;
    }
  }

  public getText(items: ContentLink[]) {
    return items.filter(item => item.isText);
  }

  ngOnInit() {
    this.companiesService.getCompanyForCurrentUser().subscribe(company => {
      this.company = company;
      this.contentLinksService.getItems({
        filter: `slDirectionLookupId eq ${this.company.id} or slDirectionLookup eq null`
      }).subscribe((items: ContentLink[]) => {
        this.getItemsImg(items);
        this.itemsText = this.getText(items);
      });
    });
  }
}
