import { Component, OnInit, Inject } from '@angular/core';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';
import { Employee } from 'src/app/staff/services/employees/employee';

@Component({
  selector: 'app-personal-page',
  templateUrl: './personal-page.component.html',
  styleUrls: ['./personal-page.component.scss']
})
export class PersonalPageComponent implements OnInit {
  public employee: Employee;
  private appShowSidebar: boolean = true;
  private appContainerStyle = { 'background-color': '#f7f7f7' };

  constructor(
    @Inject('EmployeesFactory') private employeesFactory: EmployeesFactory
  ) {}

  ngOnInit() {
    this.employeesFactory.getCurrentEmployee().subscribe(e => {
      this.employee = e;
    });
  }
}
