import { Component, OnInit, Inject } from '@angular/core';
import { Observable, of } from 'rxjs';
import { flatMap } from 'rxjs/operators';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';
import { Employee } from 'src/app/staff/services/employees/employee';

@Component({
  selector: 'app-my-info',
  templateUrl: './my-info.component.html',
  styleUrls: ['./my-info.component.scss']
})
export class MyInfoComponent implements OnInit {
  public entourage = [
    {
      position: 'Руководитель',
      name: 'Аватаркин Константин Викторовия',
      full_position: 'Руководитель отдела дизайна'
    },
    {
      position: 'Заместитель',
      name: 'Справочкина Елена Владимировна',
      full_position: 'Технический дизайнер'
    }
  ];

  public thanks = [
    {
      icon: 'url(assets/icons/personal/icon-personal-thanks-1.svg)',
      text: 'За оперативное решение вопроса'
    },
    {
      icon: 'url(assets/icons/personal/icon-personal-thanks-2.svg)',
      text: 'За вежливость'
    },
    {
      icon: 'url(assets/icons/personal/icon-personal-thanks-3.svg)',
      text: 'За решение нетривиальной задачи'
    },
    {
      icon: 'url(assets/icons/personal/icon-personal-thanks-4.svg)',
      text: 'За оперативное решение вопроса'
    },
    {
      icon: 'url(assets/icons/personal/icon-personal-thanks-5.svg)',
      text: 'За вежливость'
    }
  ];

  public deputyEmployee: Employee;
  public managerEmployee: Employee;
  public employee: Employee;

  constructor(@Inject('EmployeesFactory') private employeesFactory: EmployeesFactory) {}

  private getEmployeeById(id: number): Observable<Employee> {
    return this.employeesFactory.getEmployeeById(id);
      //.pipe(flatMap((employees: Employee[]) => of(employees[0])));
  }

  private getEntourage() {
    this.employeesFactory.getCurrentEmployee().subscribe(e => {
      this.employee = e;
      if (e.lookupDeputyId) {
        this.getEmployeeById(e.lookupDeputyId).subscribe(
          (employee: Employee) => (this.deputyEmployee = employee)
        );
      }
      if (e.lookupManagerId) {
        this.getEmployeeById(e.lookupManagerId).subscribe(
          (employee: Employee) => (this.managerEmployee = employee)
        );
      }
    });
  }

  ngOnInit() {
    this.getEntourage();
  }
}
